import React from 'react';
import styled from '@emotion/styled';
import { Link } from 'gatsby';
import SearchForm from '../SearchForm';
import { PostListItemType } from 'types/PostItem.types';

const Container = styled.header`
  display: flex;
  align-items: center;
  width: 100%;
  min-height: 10vh;
  padding: 20px 40px;
  box-sizing: border-box;

  @media (max-width: 768px) {
    padding: 20px 25px;
  }
`;

const HeaderWrapper = styled.div`
  display: flex;
  justify-content: space-between;
  width: 100%;
`;

const LogoLinkButton = styled.div`
  font-weight: 700;
  font-size: 20px;
`;

const LinkButton = styled.div`
  font-weight: 600;
  font-size: 17px;
`;

const FrontSection = styled.div`
  font-size: 20px;
`;
const LinkSection = styled.div`
  display: flex;
`;

const Header = function (posts: PostListItemType[]) {
  console.log(posts);
  return (
    <Container>
      <HeaderWrapper>
        <FrontSection>
          <Link to="/">
            <LogoLinkButton>Dong-dev</LogoLinkButton>
          </Link>
        </FrontSection>
        <LinkSection>
          <Link to="/info">
            <LinkButton>Info</LinkButton>
          </Link>
          <SearchForm posts={posts} />
        </LinkSection>
      </HeaderWrapper>
    </Container>
  );
};

export default Header;
